import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import {
  Auth,
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  questionTypesStoreInterface,
  setSelectedCourse,
  loadQuestionBank,
  loadQuestionTypes,
  setSelectedQuestion,
  QuestionAnswersStore,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import * as Interfaces from "../../../interfaces/index";
import { course, QuestionInterface } from "../../../interfaces/index";
import Loading from "../../../components/Loading/Loading";
import { getQuestionType } from "../../../utils/question_bank";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import Courses from "../../../components/Courses/Courses";
import { ChooseMethod } from "../AddQuestion/ChooseMethod/ChooseMethod";
import EditQuestion from "../AddQuestion/UploadQuestion/EditQuestion/EditQuestion";
import { LoadingType } from "../../../components/Loading/LoadingV2";
import QuestionDetails from "../../DoExam/QuestionDetails/QuestionDetails";
import { exam_question } from "../../../interfaces/exam_question";
import NoResult, {
  NoResultThemes,
} from "../../../components/NoResult/NoResult";
import SearchData from "../../../utils/search";

interface QuestionsListProps {
  auth: Auth;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionBank: QuestionBankStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory: (
    education_category: Interfaces.education_category
  ) => void;
  setSelectedSchoolLevel: (education_category: Interfaces.school_level) => void;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
  loadQuestionBank: (
    course_id: string,
    callback: (loading: boolean) => void
  ) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  setSelectedQuestion: (question: QuestionInterface) => void;
}
interface QuestionsListState {
  loading: boolean;
  searchQuestionInput: string;
  selectCourse: boolean;
  addQuestion: boolean;
  editQuestion: QuestionInterface | null;
  preview: QuestionInterface | null;
}

class _QuestionsList extends Component<QuestionsListProps, QuestionsListState> {
  constructor(props: QuestionsListProps) {
    super(props);

    this.state = {
      loading: false,
      searchQuestionInput: "",
      selectCourse: false,
      addQuestion: false,
      editQuestion: null,
      preview: null,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  setAddQuestion = (status: boolean) => {
    this.setState({ addQuestion: status });
  };

  chooseCourse = (course: course | null) => {
    this.props.setSelectedCourse(course);
    if (course !== null) {
      this.props.loadQuestionBank(course.course_id, this.setLoading);
    }
    this.setState({ selectCourse: false });
  };

  answerQuestionFn = (answer: QuestionAnswersStore) => {};
  setSelectedQuestion = (question: exam_question) => {};

  componentDidMount = () => {
    if (
      this.props.questionTypes.questionTypes === null ||
      this.props.questionTypes.questionTypes.length === 0
    ) {
      this.props.loadQuestionTypes(this.setLoading);
    }
    if (
      this.props.course.courses === null ||
      this.props.course.courses.length === 0
    ) {
      this.props.loadCourses(this.setLoading);
    }
    if (this.props.course.selected_course === null) {
      this.setState({ selectCourse: true });
    } else {
      this.setState({ selectCourse: false });
    }
    if (
      this.props.questionBank.questions === null ||
      this.props.questionBank.questions.length === 0
    ) {
      if (this.props.course.selected_course !== null) {
        this.props.loadQuestionBank(
          this.props.course.selected_course.course_id,
          this.setLoading
        );
      }
    }
  };

  setEditQuestion = (item: QuestionInterface) => {
    this.props.setSelectedQuestion(item);
    this.setState({ editQuestion: item });
  };

  unSelectQuestion = () => {
    this.setState({ editQuestion: null });
  };

  render() {
    return (
      <Fragment>
        {this.state.editQuestion === null ? (
          <div className="">
            <div className="mt-3 rounded px-2 flex flex-col gap-3 bg-white py-2">
              <div className="flex flex-row items center justify-between">
                <div className="flex flex-col px-1">
                  <span className="text-gray-600 text-3xl font-bold">
                    Question Bank
                  </span>
                  <span className="text-blue-600 text-sm font-bold">
                    Course:{" "}
                    {this.props.course.selected_course === null ? (
                      <span className="text-yellow-700 italic">
                        Not selected!
                      </span>
                    ) : (
                      `${this.props.course.selected_course.course_code} - ${this.props.course.selected_course.course_name}`
                    )}
                  </span>
                </div>
                <div className="flex flex-row items-center gap-2">
                  {this.props.course.selected_course !== null && (
                    <div
                      onClick={() => this.setState({ selectCourse: true })}
                      className={`btn cursor-pointer ${
                        this.props.course.selected_course === null
                          ? "bg-yellow-50 hover:bg-yellow-100  text-yellow-700 border border-yellow-200"
                          : "bg-blue-50 hover:bg-blue-100  text-blue-600"
                      } h-max flex items-center justify-center h-10`}
                    >
                      {this.props.course.selected_course === null
                        ? "Choose course"
                        : "Change course"}
                    </div>
                  )}
                  <div
                    onClick={() => this.setState({ addQuestion: true })}
                    className="btn cursor-pointer bg-white hover:bg-blue-100 border border-blue-600 text-blue-600 h-max flex items-center justify-center h-10"
                  >
                    Add questions
                  </div>
                </div>
              </div>
              <input
                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                  this.setState({ searchQuestionInput: e.target.value })
                }
                type="text"
                className="input-white border h-10 text-sm border-gray-400"
                placeholder="Search question by title or description"
              />
            </div>
            <div className="flex-col">
              <div className="flex my-2 flex-row justify-between items-center">
                <span className="text-xl font-bold text-blue-600">
                  List of questions
                </span>
                <div className="underline font-bold text-blue-700 bg-white rounded-full px-3 py-1">
                  {this.props.questionBank.questions &&
                    this.props.questionBank.questions.length}{" "}
                  questions
                </div>
              </div>
              <div className="flex flex-col">
                <div className="bg-white flex rounded">
                  <table className="table-auto w-full">
                    <thead>
                      <tr className="text-left bg-blue-50 border-b-2 border-blue-500 text-blue-600">
                        <th className="p-3 w-1/12 pl-10">#</th>
                        <th className="p-3">Question</th>
                        <th>Category</th>
                        <th>Marks</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.loading === true ? (
                        <tr>
                          <td colSpan={5} className="pt-6">
                            <Loading />
                          </td>
                        </tr>
                      ) : this.props.course.selected_course === null ? (
                        <tr>
                          <td colSpan={5}>
                            <div className="bg-gray-100 flex flex-col items-center text-center gap-3 rounded px-3 py-3">
                              <span className="text-xl font-bold">
                                No course selected!
                              </span>
                              <div
                                onClick={() =>
                                  this.setState({ selectCourse: true })
                                }
                                className="btn bg-yellow-50 text-yellow-800 cursor-pointer hover:bg-white border border-white shadow hover:shadow-none hover:border-yellow-600"
                              >
                                Choose a course
                              </div>
                            </div>
                          </td>
                        </tr>
                      ) : this.props.questionBank.questions === null ? (
                        <tr>
                          <td colSpan={5} className="pt-6">
                            <Loading />
                          </td>
                        </tr>
                      ) : SearchData(
                          this.props.questionBank.questions,
                          this.state.searchQuestionInput
                        ).length === 0 ? (
                        <tr>
                          <td colSpan={5}>
                            <div className="flex flex-col items-cnter justify-center">
                              <NoResult
                                theme={NoResultThemes.Dark}
                                value={""}
                                component={
                                  <span className="font-bold text-gray-600 text-2xl -mt-4">
                                    No questions available!
                                  </span>
                                }
                              />
                            </div>
                          </td>
                        </tr>
                      ) : (
                        this.props.questionTypes.questionTypes !== null &&
                        this.props.questionTypes.questionTypes.length > 0 &&
                        SearchData(
                          this.props.questionBank.questions,
                          this.state.searchQuestionInput
                        ).map((item, i) => (
                          <tr
                            key={i + 1}
                            className="shadow-sm cursor-pointer border-none"
                          >
                            <td className="flex items-center justify-center">
                              <div className="py-1 rounded-full my-3">
                                <span className="text-xl italic text-blue-600">
                                  {i < 9 && 0}
                                  {i + 1}
                                </span>
                              </div>
                            </td>
                            <td>
                              <div className="py-2 px-3 flex flex-col">
                                <span className="text-sm text-gray-600 font-bold w-96 truncate">
                                  {item.question[0].title}
                                </span>
                                <span className="text-xs text-gray-500 w-96 truncate">
                                  {item.question[0].description}
                                </span>
                              </div>
                            </td>
                            <td>
                              <div className="text-xs font-bold text-gray-700 w-80 truncate">
                                {getQuestionType(
                                  this.props.questionTypes.questionTypes,
                                  item.type
                                ) === null ? (
                                  <span className="text-yellow-700">
                                    Invalid type
                                  </span>
                                ) : (
                                  <span className="text-blue-600">
                                    {getQuestionType(
                                      this.props.questionTypes.questionTypes,
                                      item.type
                                    )}
                                  </span>
                                )}
                              </div>
                            </td>
                            <td>
                              <span className="text-xs text-gray-500">
                                {item.marks}
                              </span>
                            </td>
                            <td>
                              <div className="flex justify-end pr-1">
                                <div
                                  onClick={() =>
                                    this.setState({ preview: item })
                                  }
                                  className="border border-gray-200 btn bg-gray-100 px-3 py-1 rounded text-gray-600 text-sm flex items-center"
                                >
                                  <span>View</span>
                                </div>
                                <div
                                  onClick={() => this.setEditQuestion(item)}
                                  className="border border-blue-200 btn bg-blue-50 px-3 py-1 rounded text-blue-600 text-sm flex items-center ml-1"
                                >
                                  <span>Edit</span>
                                </div>
                                <div className="border border-red-200 btn px-3 py-1 rounded bg-red-50 text-red-600 text-sm flex items-center ml-1">
                                  <span>Delete</span>
                                </div>
                              </div>
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div>
            {this.props.course.selected_course !== null && (
              <div className="bg-white px-2 py-2 mt-4 flex flex-col rounded-t">
                <span className="text-sm text-gray-600 font-bold">Course</span>
                <span className="font-bold text-xl text-blue-600">
                  {this.props.course.selected_course.course_code} -{" "}
                  {this.props.course.selected_course.course_name}
                </span>
              </div>
            )}
            {this.props.questionBank.questions === null ||
            this.props.questionBank.selectedQuestion === null ? (
              <div className="mt-4 px-4 py-4 rounded bg-white">
                <Loading type={LoadingType.dark} />
              </div>
            ) : (
              <EditQuestion
                selectedQuestion={this.state.editQuestion}
                questionsList={this.props.questionBank.questions}
                unSelectQuestion={this.unSelectQuestion}
              />
            )}
          </div>
        )}
        {this.state.preview !== null && (
          <Modal
            title="Question preview"
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ preview: null })}
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{ title: true }}
          >
            <QuestionDetails
              questionAnswers={undefined}
              answerQuestionFn={this.answerQuestionFn}
              selectedQuestion={null}
              setSelectedQuestion={this.setSelectedQuestion}
              preview={false}
              submitQuestionAnswer={false}
            />
          </Modal>
        )}
        {this.state.selectCourse === true && (
          <Modal
            title="Choose a course"
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ selectCourse: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{ title: true }}
          >
            <Courses
              setSelectedCourse={this.chooseCourse}
              selected_course_id={
                this.props.course.selected_course &&
                this.props.course.selected_course.course_id
              }
            />
          </Modal>
        )}
        {this.state.addQuestion === true && (
          <Modal
            title={
              <div className="flex flex-col">
                <span className="text-gray-800 text-3xl font-extrabold">
                  Choose method
                </span>
                <span className="text-gray-600 text-sm">
                  Prepare question bank by adding questions through two main
                  methods
                </span>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ addQuestion: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{ title: true }}
          >
            <ChooseMethod close={this.setAddQuestion} />
          </Modal>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  questionTypes,
}: StoreState): {
  auth: Auth;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionBank: QuestionBankStoreInterface;
  questionTypes: questionTypesStoreInterface;
} => {
  return {
    auth: auth,
    course: course,
    schoolLevels: schoolLevels,
    questionBank: questionBank,
    questionTypes: questionTypes,
  };
};

export const QuestionsList = connect(mapStateToProps, {
  loadCourses,
  setSelectedCourse,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  loadQuestionBank,
  loadQuestionTypes,
  setSelectedQuestion,
})(_QuestionsList);
