import React, { useEffect } from "react";
import { MdRadioButtonUnchecked } from "react-icons/md";
import { RiCheckboxCircleFill } from "react-icons/ri";
import { QuestionAnswersStore } from "../../../../actions";
import { CandidateExam } from "../../../../interfaces/exam";
import {
  MatchingQuestionAnswerInterface,
  MatchingStatuses,
  QuestionInterface,
  QuestionLanguage,
  QuestionLanguages,
  questionTypeElementsType,
} from "../../../../interfaces/question";
import { Shuffle } from "../../../../utils/functions";

interface MatchingProps {
  selectedExam: CandidateExam | null;
  selectedLanguage: QuestionLanguage;
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null;
    answers: MatchingQuestionAnswerInterface[] | undefined;
  };
  answerQuestionFn: Function;
}

const Matching = (props: MatchingProps) => {
  const addAnswerItemFn = (newAnswers: MatchingQuestionAnswerInterface[]) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.answerQuestionFn({
        question_id: props.selectedQuestionAnswer.question_id,
        status: false,
        answer: newAnswers,
      });
    }
  };
  const checkAnswer = (item: MatchingQuestionAnswerInterface): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
      if (check !== undefined) {
        let x = check.answer.find((itm) => itm.id === item.id);
        if (x !== undefined && x.status !== "") {
          res = true;
        }
      }
    }
    return res;
  };

  const addAnswer = (
    item: MatchingQuestionAnswerInterface,
    question_id: string | null
  ) => {
    let oldAnswers = props.available_answers.find(
      (itm) => itm.question_id === question_id
    );
    if (oldAnswers !== undefined) {
      let newAnswers: MatchingQuestionAnswerInterface[] = [];
      for (let x of oldAnswers.answer) {
        if (x.id === item.id) {
          newAnswers = [
            ...newAnswers,
            {
              id: item.id,
              value: item.value,
              status: item.status,
            },
          ];
        } else {
          newAnswers = [...newAnswers, x];
        }
      }
      addAnswerItemFn(newAnswers);
    }
  };

  useEffect(() => {
    let test = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (
      test === undefined ||
      (test !== undefined && test.answer.length === 0)
    ) {
      if (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer !== undefined &&
        props.selectedQuestionAnswer.question_id !== null
      ) {
        let newAnswers: MatchingQuestionAnswerInterface[] = [];
        for (let a of props.selectedQuestionAnswer.answers) {
          newAnswers = [
            ...newAnswers,
            {
              id: a.id,
              value: a.value,
              status: "",
            },
          ];
        }
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: true,
          answer: newAnswers,
        });
      }
    }
  }, [props]);

  const checkAnswerStatus = (
    selectedQuestionId: string,
    selectedLang: QuestionLanguage,
    ans: MatchingQuestionAnswerInterface
  ) => {
    if (props.selectedExam !== null) {
      let testing = props.selectedExam.questions.find(
        (test) => test.question_id === selectedQuestionId
      );
      if (testing !== undefined) {
        for (let lang of QuestionLanguages) {
          let testLang = testing.question.find(
            (itm) => itm.language === lang.key
          );
          if (testLang !== undefined) {
            let testAnswerLang = testLang.answer.find(
              (it) => it.status.toString() === ans.status
            );
            if (testAnswerLang !== undefined) {
              return testAnswerLang.status.toString();
            }
          }
        }
      }
    }
    return false;
  };

  const checkIfSelected = (x: string) => {
    let temp = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (temp !== undefined) {
      let ans = temp.answer.find((itm) => itm.id === x);
      if (ans !== undefined && ans.status !== "") {
        if (props.selectedQuestionAnswer.question_id !== null) {
          return checkAnswerStatus(
            props.selectedQuestionAnswer.question_id,
            props.selectedLanguage,
            ans
          );
        }
      }
    }
    return false;
  };

  const ReturnMatchingStatus = (): MatchingStatuses[] => {
    let res: MatchingStatuses[] = [];
    let question: QuestionInterface | undefined =
      props.selectedExam?.questions.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
    if (question !== undefined) {
      for (let item of question.question) {
        for (let ans of item.answer) {
          res = [
            ...res,
            {
              id: ans.id,
              lang: item.language,
              status: ans.status.toString(),
            },
          ];
        }
      }
    }
    return res;
  };

  const findWord = (lang: QuestionLanguage, status: string | false) => {
    let res: string = "";
    let arr: MatchingStatuses[] = ReturnMatchingStatus();
    if (arr.length > 0) {
      let item = arr.find((itm) => itm.status === status)?.id;
      if (item !== undefined) {
        let newItem = arr.find(
          (itm) => itm.id === item && itm.lang === lang
        )?.status;
        if (newItem !== undefined) {
          res = newItem;
        }
      }
    }
    return res;
  };

  return (
    <div className="">
      {props.selectedQuestionAnswer !== null &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer.answers.map((item) => (
          <div
            className={`px-2 py-2 border-b ${
              checkAnswer(item) === true ? "text-blue-600 border-blue-200" : ""
            } grid grid-cols-2`}
          >
            <div className="col-span-1 flex flex-row items-center gap-2">
              {checkAnswer(item) === true ? (
                <RiCheckboxCircleFill className="text-2xl" />
              ) : (
                <MdRadioButtonUnchecked className="text-2xl" />
              )}
              <span className="ml-2">{item.value}</span>
            </div>
            <div className="col-span-1">
              <select
                onChange={(e) => {
                  addAnswer(
                    {
                      id: item.id,
                      value: item.value,
                      status: e.target.value,
                    },
                    props.selectedQuestionAnswer.question_id
                  );
                }}
                className={`border rounded w-full py-2 px-2 cursor-pointer focus:outline-none focus:border-blue-500 ${
                  checkAnswer(item) === false
                    ? "border-gray-400 text-gray-600"
                    : "border-blue-50 bg-blue-50 text-blue-600 hover:border-blue-600"
                }`}
              >
                <option value="">Select matching</option>
                {props.selectedQuestionAnswer !== null &&
                  props.selectedQuestionAnswer.answers !== undefined &&
                  Shuffle(props.selectedQuestionAnswer.answers).map(
                    (itm, x) => (
                      <option
                        selected={
                          itm.status ===
                          findWord(
                            props.selectedLanguage,
                            checkIfSelected(item.id)
                          )
                            ? true
                            : false
                        }
                        key={x + 1}
                        value={itm.status}
                      >
                        {itm.status}
                      </option>
                    )
                  )}
              </select>
            </div>
          </div>
        ))}
    </div>
  );
};

export default Matching;
